<template>
  <v-container fluid>
    <v-layout justify-center row>
      <v-flex xs12 md6>
        <v-card>
          <v-card-title>Авторизация</v-card-title>
          <v-card-text>
            <v-container grid-list-md>
              <v-layout row>
                <v-flex xs12 md6>
                  <v-text-field v-model="email" label="E-mail" required prepend-icon="mdi-at" :rules="emailRules"
                    hint="Валидный E-mail: example@gmail.com"></v-text-field>
                </v-flex>
                <v-flex xs12 md6>
                  <v-text-field v-model="password" :type="showPass ? 'text' : 'password'" label="Пароль" required
                    prepend-icon="mdi-lock" :append-icon="showPass ? 'mdi-eye-outline' : 'mdi-eye-off-outline'"
                    :rules="passwordRules" counter @click:append="showPass = !showPass"
                    hint="Пароль должен содержать более 6 символов"></v-text-field> 
                </v-flex>
              </v-layout>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-layout>
              <v-dialog v-model="dialog" presistent max-width="800">
                <template v-slot:activator="{on}">
                  <v-btn text v-on="on" color="primary">Нет аккаунта?</v-btn>
                </template>
                <v-card>
                  <v-card-title>Регистрация</v-card-title>
                  <v-card-text>
                    <v-container grid-list-md>
                      <v-layout wrap>
                        <v-flex xs12>
                          <v-text-field  v-model="remail" label="E-mail" required prepend-icon="mdi-at" :rules="emailRules"
                            hint="Валидный E-mail: example@gmail.com"></v-text-field>
                        </v-flex>
                        <v-flex xs12 md6>
                          <v-text-field :error="validPass" v-model="rpassword" :type="rshowPass ? 'text' : 'password'" label="Пароль" required
                            prepend-icon="mdi-lock" :append-icon="rshowPass ? 'mdi-eye-outline' : 'mdi-eye-off-outline'"
                            :rules="passwordRules" counter @click:append="rshowPass = !rshowPass"
                            hint="Пароль должен содержать более 6 символов"></v-text-field>
                        </v-flex>
                        <v-flex xs12 md6>
                          <v-text-field :error="validPass"  v-model="crpassword" :type="rshowPass ? 'text' : 'password'" label="Подтвердите пароль" required
                            prepend-icon="mdi-lock" :append-icon="rshowPass ? 'mdi-eye-outline' : 'mdi-eye-off-outline'"
                            :rules="passwordRules" counter @click:append="rshowPass = !rshowPass"
                            hint="Пароль должен содержать более 6 символов"></v-text-field>
                        </v-flex>
                      </v-layout>
                    </v-container>
                  </v-card-text>
                  <v-card-actions>
                    <v-btn text @click="dialog = false">Отмена</v-btn>
                    <v-spacer></v-spacer>
                    <v-btn 
                      color="teal lighten-1" 
                      outlined 
                      @click="registration"
                      :loading="loading"
                      >Зарегистрироваться</v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-layout>
            <v-spacer></v-spacer>
            <v-btn color="cyan lighten-4" @click="login" :loading="loading">Авторизоваться</v-btn>
          </v-card-actions>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  export default {
    data() {
      return {
        custome: true,
        dialog: false,
        showPass: false,
        rshowPass: false,
        email: '',
        password: '',
        remail: '',
        rpassword: '',
        crpassword: '',
        emailRules: [
          v => !!v || 'Поле E-mail не должно быть пустым',
          v => /.+@.+/.test(v) || 'Введите валидный E-mail'
        ],
        passwordRules: [
          v => !!v || 'Пароль не должен быть пустым',
          v => v.length >= 6 || 'Пароль должен содержать более 6 символов!'
        ],
        rRules: [v => !!v || 'Поле должно быть заполнено данными']
      }
    },
    methods: {
      registration() {
        this.$store.dispatch('registrationUser', {
          email: this.remail,
          password: this.rpassword
        })
        .then(() => {
          this.$store.dispatch('setDrawer', true)
          this.$router.push('/')
        })
      },
      login() {
        this.$store.dispatch('loginUser', {
          email: this.email,
          password: this.password
        })
        .then(() => {
          this.$store.dispatch('setDrawer', true)
          this.$router.push('/')
        })
      }
    },
    computed: {
      validPass () {
        return this.rpassword === this.crpassword ? false : true
      },
      loading () {
        return this.$store.getters.loading
      }
    },
    mounted () {
      this.$store.dispatch('setDrawer', false)
      if (this.$route.query['loginError']) {
        this.$store.dispatch('setError', 'Log in please!')
      }
    }
  }
</script>